<template>
  <div >
    <div class="container demo-2">
      <div class="content">
          <div id="large-header" class="large-header" :style="{backgroundImage: 'url('+bg+')'}">
              <canvas id="demo-canvas"></canvas>
              <h3 class="main-title"><span>I AM 施伟达</span></h3>
          </div>
    <ul class="menu">
      <li><a href="https://sweida.github.io/gitblog/#/Blog/BlogList">博客</a></li>
      <li><a href="https://sweida.github.io/gitblog/#/AboutMe">关于我</a></li>
    </ul>
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'HelloWorld',
  data () {
    return {
      bg:require('../assets/demo-2-bg.jpg'),
      msg: '欢迎来到德莱联盟！哈哈'
    }
  },
  mounted() {
    (function() {

        var width, height, largeHeader, canvas, ctx, circles, target, animateHeader = true;

        // Main
        initHeader();
        addListeners();

        function initHeader() {
            width = window.innerWidth;
            height = window.innerHeight;
            target = {x: 0, y: height};

            largeHeader = document.getElementById('large-header');
            largeHeader.style.height = height+'px';

            canvas = document.getElementById('demo-canvas');
            canvas.width = width;
            canvas.height = height;
            ctx = canvas.getContext('2d');

            // create particles
            circles = [];
            for(var x = 0; x < width*0.5; x++) {
                var c = new Circle();
                circles.push(c);
            }
            animate();
        }

        // Event handling
        function addListeners() {
            window.addEventListener('scroll', scrollCheck);
            window.addEventListener('resize', resize);
        }

        function scrollCheck() {
            if(document.body.scrollTop > height) animateHeader = false;
            else animateHeader = true;
        }

        function resize() {
            width = window.innerWidth;
            height = window.innerHeight;
            largeHeader.style.height = height+'px';
            canvas.width = width;
            canvas.height = height;
        }

        function animate() {
            if(animateHeader) {
                ctx.clearRect(0,0,width,height);
                for(var i in circles) {
                    circles[i].draw();
                }
            }
            requestAnimationFrame(animate);
        }

        // Canvas manipulation
        function Circle() {
            var _this = this;

            // constructor
            (function() {
                _this.pos = {};
                init();
                console.log(_this);
            })();

            function init() {
                _this.pos.x = Math.random()*width;
                _this.pos.y = height+Math.random()*100;
                _this.alpha = 0.1+Math.random()*0.3;
                _this.scale = 0.1+Math.random()*0.3;
                _this.velocity = Math.random();
            }

            this.draw = function() {
                if(_this.alpha <= 0) {
                    init();
                }
                _this.pos.y -= _this.velocity;
                _this.alpha -= 0.0005;
                ctx.beginPath();
                ctx.arc(_this.pos.x, _this.pos.y, _this.scale*10, 0, 2 * Math.PI, false);
                ctx.fillStyle = 'rgba(255,255,255,'+ _this.alpha+')';
                ctx.fill();
            };
        }

    })();

    (function() {
        var lastTime = 0;
        var vendors = ['ms', 'moz', 'webkit', 'o'];
        for(var x = 0; x < vendors.length && !window.requestAnimationFrame; ++x) {
            window.requestAnimationFrame = window[vendors[x]+'RequestAnimationFrame'];
            window.cancelAnimationFrame = window[vendors[x]+'CancelAnimationFrame']
                || window[vendors[x]+'CancelRequestAnimationFrame'];
        }

        if (!window.requestAnimationFrame)
            window.requestAnimationFrame = function(callback, element) {
                var currTime = new Date().getTime();
                var timeToCall = Math.max(0, 16 - (currTime - lastTime));
                var id = window.setTimeout(function() { callback(currTime + timeToCall); },
                    timeToCall);
                lastTime = currTime + timeToCall;
                return id;
            };

        if (!window.cancelAnimationFrame)
            window.cancelAnimationFrame = function(id) {
                clearTimeout(id);
            };
    }());
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
li{
  list-style: none;
}
.menu{
  position: absolute;
  top: 20%;
  z-index: 10000;
  right: 0;
  left: 0;
  margin: auto;
  padding: 0;
  font-size: 32px;
}
.menu li {
  line-height: 70px;
  width: 200px;
  border: 2px solid #fff;
  margin: 65px auto;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.4);

}
.menu li a {
  color: #fff;
}
/* Header */
.large-header {
  position: relative;
  width: 100%;
  background: #333;
  overflow: hidden;
  background-size: cover;
  background-position: center center;
  z-index: 1;
}

.demo-2 .large-header {
  /*background-image: url('../assets/demo-2-bg.jpg');*/
  background-position: center bottom;
}

.demo-2 .main-title {
  /*font-family: 'Clicker Script', cursive;*/
  position: absolute;
  color: #fbfff5;
  top: 78%;
  left: 0;
  right: 0;
  margin: auto;
  font-weight: normal;
  word-spacing: 15px;
  font-size: 4em;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.4);
}

/*.demo-2 .main-title::before {
  content: '';
  width: 20vw;
  height: 20vw;
  min-width: 3.5em;
  min-height: 3.5em;
  background: url(../assets/deco.svg) no-repeat center center;
  background-size: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  z-index: -1;
  -webkit-transform: translate3d(-50%,-50%,0);
  transform: translate3d(-50%,-50%,0);
}*/


@media only screen and (max-width : 768px) {

  .demo-2 .main-title {
    font-size: 30px;
  }
  .menu li{
    line-height: 60px;
    width: 180px;
        font-size: 26px;
  }
}


</style>



